.hero-about-company-two {

    &__container {
        @media (max-width:$tablet-max) {
            padding: 0;
        }
    }

    .hero-primary {
        margin-top: 30px;
        padding: 0;
        background: transparent;
        border-radius: 0;

        &__text {
            max-width: 1180px;
        }
    }

    &__inner {
        @media (max-width:$tablet-max) {
            padding: 32px 16px;
        }
    }

    &__title {}

    &__text {
        p {
            max-width: 775px;
        }
    }

    &__content {
        @include adaptiveValue("margin-top", 74, 32, 1296, 576, 1);
        width: 100%;
        background: linear-gradient(136deg, #6792FF 0%, #2B53E1 83.35%), #F3F7FF;
        @include adaptiveValue("border-radius", 70, 24, 1296, 576, 1);
        height: 720px;
        max-height: 720px;

        position: relative;
        overflow: hidden;
        padding: 86px;

        display: flex;
        justify-content: space-between;

        .col {
            &:nth-child(1) {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                width: 481px;
            }

            &:nth-child(2) {
                display: flex;
                flex-direction: column;
                width: 381px;

                .card-info-third {
                    transition: transform $transitionSpeed ease-out;
                    &:nth-child(1) {
                        min-height: 360px;
                        height: 360px;
                        @media (min-width:$desktop-min) {
                            &:hover {
                                +.card-info-third {
                                    transform: translateY(-31.5%);

                                    +.card-info-third {
                                        transform: translateY(-123%);
                                    }
                                }
                            }
                        }
                    }

                    &:nth-child(2) {
                        min-height: 360px;
                        height: 360px;
                        transform: translateY(-61.5%);

                        @media (min-width:$desktop-min) {
                            &:hover {
                                +.card-info-third {
                                    transform: translateY(-125%);
                                }
                            }
                        }
                    }

                    &:nth-child(3) {
                        min-height: 270px;
                        height: 270px;
                        transform: translateY(-163%);
                    }
                }
            }
        }
    }

    &__tags {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        row-gap: 8px;
        column-gap: 5px;

        @media (max-width:$tablet-max) {
            justify-content: center;
        }
    }

    &__tag {
        height: 62px;

        border-radius: 40px;
        border: 1.8px solid rgba(180, 199, 245, 0.00);
        background: rgba(255, 255, 255, 0.13);
        padding: 0 32px;
        @include adaptiveValue("font-size", 16, 14, 1296, 576, 1);
        font-weight: 500;
    }

    &__info {
        span {
            display: block;
            color: var(--white);

            &:nth-child(1) {
                @include adaptiveValue("font-size", 60, 32, 1296, 576, 1);
                line-height: math.div(99, 60);
                font-weight: 500;
            }

            &:nth-child(2) {
                max-width: 230px;
                @include adaptiveValue("font-size", 18, 14, 1296, 576, 1);
                line-height: math.div(28, 18);
            }
        }

        @media (max-width:$tablet-max) {
            margin-top: 32px;
            text-align: center;
        }
    }
}
